/* The Success Message */
.congrats {
  position: relative;
  z-index: 100;
  padding: 10px 20px;
  border-radius: 8px;
  text-align: center;
  background-color: #48BB78; /* Green background for success */
  font-weight: bold;
  font-size: 16px;
}

/* Confetti animation */
.congrats-container {
  position: relative;
}

.confetti {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 99;
}

.confetti::before,
.confetti::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.3);
  animation: confetti 1.5s infinite;
}

.confetti::before {
  animation-delay: 0s;
}

.confetti::after {
  animation-delay: 0.3s;
}

@keyframes confetti {
  0% {
    transform: translate(0, 0) rotate(0deg);
    opacity: 1;
  }
  25% {
    opacity: 0.7;
  }
  50% {
    transform: translate(30px, 50px) rotate(180deg);
    opacity: 0.6;
  }
  75% {
    transform: translate(-30px, -40px) rotate(360deg);
    opacity: 0.8;
  }
  100% {
    transform: translate(0, 0) rotate(0deg);
    opacity: 1;
  }
}


