			20% {width: 10%;}
			40% {width: 30%;}
			50% {width: 60%;}
			100% {width: 90%;}
		}

		.progress-bar {
			display: flex;
			height: 100%;
			width: 100%;
			background: linear-gradient(to right, #F5BB5F, #FF9A8B, #FFD1A1, #D8A738);
			animation: progress-animation 5s ease-in-out;
		}

    
